*{
    @font-face {
        font-family: 'fuente1' ;
        src: url(../../Fonts/NightmareBeforeChristmas-2v8e.ttf);
      }
}
.navbar-container{ 
  background-color: black;
  width: 100%;
  height: 100px;
  display: inline-flex;
  align-items: center;
  justify-content: space-around;
  align-content: flex-start;
  flex-direction: row;
  flex-wrap: nowrap;
 
  
.navbar{
  height: 120px;
  position: relative;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  
  .navbar-logo {
    width: 57px;
    height: 57px;
   
  }
  h3{
   color: white;
   width: 200px; 
   font-size: 3.5rem;         
   font-size: auto;
   font-family: 'fuente1';

   &:hover{
    color: #ce30ce;
    font-weight: bolder;
  }
  }
  
}
  
a{
  
  text-decoration: none;
  list-style: none;
  color: #ffff;
  font-weight: bold;
  font-size: 2.4rem;
  font-family: 'fuente1';
  &:hover{
    color: #ce30ce;
  }

}
.cart-container{ 
  display: flex;
  flex-wrap: wrap;
  align-items: center;

  p{
  color: #ce30ce;
  font-family:' fuente1';
  &:hover{
    color: rgb(56, 139, 56);
  
  }
}
  }img.cartIcon {
      width: 40px;
      height: 39px;
  
  }
  
.active-link{
  width: 120px;
  border: solid white 5px;
   border-radius:5px;
   justify-content: center;
   align-items:center;
   display: flex;
   &:hover{
    border: solid #ce30ce 5px;
   }
}
}
